.home-section {
  padding: 80px 0;
}

.services-section {
  background-color: #fff;
}

.services-wrap {
  display: flex;
  justify-content: space-between;
}

.services-item {
  width: 23%;
  border-radius: 4px;
  overflow: hidden;
}

.services-item:hover img {
  transform: scale(1.1);
}

.services-item .service-thumbnail {
  display: block;
  width: 100%;
  height: 270px;
  overflow: hidden;
  margin-bottom: 15px;
}

.services-item img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

.services-item p {
  font-size: 22px;
  font-weight: 500;
  color: #1F283C;
  line-height: 30px;
}

#services-wrap_mobile {
  display: none;
}

@media screen and (max-width: 990px) {
  .services-wrap {
    display: none;
  }
  #services-wrap_mobile {
    display: block;
  }
  #services-wrap_mobile .owl-stage {
    padding-left: 60px !important;
  }
}

@media screen and (max-width: 500px) {
  .home-section {
    padding: 35px 0;
  }
  #services-wrap_mobile .owl-stage {
    padding-left: 20px !important;
  }
  #services-wrap_mobile .owl-dots {
    display: none;
  }
  .services-item {
    display: block;
    width: 100%;
    height: 100%;
  }
  .services-item .service-thumbnail {
    height: 240px;
  }
  .services-item p {
    font-size: 21px;
  }
}

